.colaborador {
    width: 280px;
    position: relative;
    border-radius: 10px;
    background-color: white;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
}

.colaborador .deletar {
    top: -10px;
    right: -10px;
    cursor: pointer;
    position: absolute;
}

.colaborador .cabecalho {
    border-radius: 10px 10px 0px 0px;
}
.colaborador img {
    width: 100px;
    bottom: -50px;
    border-radius: 50%;
    position: relative;
}

.colaborador .rodape {
    padding-top: 90px;
    padding-bottom: 40px;
    background: #ffffff;
    border-radius: 0px 0px 10px 10px;
}

.colaborador .rodape h4 {
    font-size: 18px;
    color: #6278f7;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}

.colaborador .rodape h5 {
    font-size: 18px;
    padding: 0 16px;
    color: #212121;
    line-height: 22px;
}

.favoritar {
    margin-top: 20px;
}
