.time {
    padding: 32px;
    text-align: center;
    position: relative;
    background-blend-mode: multiply;
}

.time h3 {
    font-size: 32px;
    padding-bottom: 8px;
    font-family: "Prata";
    display: inline-block;
    border-bottom: 4px solid;
}

.time .colaboradores {
    gap: 50px;
    display: flex;
    flex-wrap: wrap;
    margin-top: 32px;
    justify-content: center;
}

.input-cor {
    top: 20px;
    right: 50px;
    position: absolute;
}
